export default {
	name:'bottom-nav',
	data() {
		return {
			// 选中项
			select: 0
		};
	},
	
	props:{
		/**
		 * 底部导航栏选中项
		 */
		current: {
		  type: Number,
		  default: 0
		},
		/**
		 * 底部导航栏项列表
		 */
		list: {
		  type: Array,
		  default: () => {
		    return [{
		        "pagePath": "/pages/tabbar/home/home",
		        "text": "首页",
		        "icon": "home",
						
		      },
		      {
		        "pagePath": "/pages/tabbar/my/my",
		        "text": "我的",
		        "icon": "home"
		      },
					{
					  "pagePath": "/pages/tabbar/my/my",
					  "text": "他的",
					  "icon": "home"
					}
		    ];
		  }
		},
		/**
		 * 文字颜色
		 */
		fontColor: {
		  type: String,
		  default: ''
		},
		/**
		 * 底部上边线颜色
		 */
		borderColor: {
		  type: String,
		  default: ''
		},
		/**
		 * 背景颜色
		 */
		backgroundColor: {
		  type: String,
		  default: '#619ad6'
		},
		/**
		 * 激活文字颜色
		 */
		activeFontColor: {
		  type: String,
		  default: '#1f1cd6'
		},
	},
	created() {
		this.select=this.$store.state.app.bottomNavCurrent
	},
	computed: {
	  /**
	   * 底部导航栏颜色样式
	   */
	  setColorStyle() {
	    let colorStyle = {};
	    // 文字颜色
	    if (this.fontColor) {
	      colorStyle.color = this.fontColor;
	    }
	    // 上边线颜色
	    if (this.borderColor) {
	      colorStyle.borderTop=`1px ${this.borderColor} solid;`;
	    }
	    // 背景颜色
	    if (this.backgroundColor) {
	      colorStyle.background =this.backgroundColor;
	    }
	    return colorStyle;
	  },
	  /**
	   * 激活文字样式
	   */
	  setActiveFontColorStyle() {
	    let activeFontColorStyle = '';
	    if (this.activeFontColor) {
	      activeFontColorStyle += `color:${this.activeFontColor};`;
	    }
	    return activeFontColorStyle;
	  }
	},
	
	methods:{
		clickNav(index){
			this.select = index;
			this.$store.commit('setBottomNavCurrent',index)
			this.$emit("on-click", {
			  "item": this.list[index],
			  "select": index
			});
		}
	}
}
